// modules
import styled from "styled-components";

// components
import Container from "../../components/Container/Container";
import Button from "../../components/Button/Button";

// etc.
import cover from '../../assets/images/cover.jpg';

const Main = () => {
  return (
    <Container>
      <Title>  </Title>
      <SubTitle>
             ?
      </SubTitle>
      <StyledImage src={cover} alt='cover' />
      <Text>
        2021 6     200 
        170       ?
      </Text>
      <Button to="/quiz">? Yes!</Button>
    </Container>
  );
};

const Title = styled.h1`
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 8px;
  text-align: center;
  color: ${(props) => props.theme.primaryColor100};
`;

const SubTitle = styled.h1`
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 24px;
  text-align: center;
  color: ${(props) => props.theme.gray80};
`;

const StyledImage = styled.img`
  border-radius: 16px;
  opacity: 80%;
  margin-bottom: 16px;
  max-width: 100%;
  display: block;
`;

const Text = styled.p`
  font-size: 16px;
  margin-bottom: 24px;
`;

export default Main;